import { SFUISchemaItem } from './schema/ui';
import { ERRORSDEFAULT } from './errors';
import { SFButton } from './interface';

export class DelonFormConfig {
  /**
   *  `ERRORSDEFAULT``[ 'type', 'enum' ]`
   *
   * - `type`  Schema  `type` 
   * - `enum` 
   */
  ingoreKeywords?: string[] = ['type', 'enum'];
  /**
   * [ajv](http://epoberezkin.github.io/ajv/#options) 
   */
  ajv?: any;
  /**
   * `true`
   * - `true` 
   * - `false` 
   */
  liveValidate? = true;
  /**
   *  `autocomplete` `on`
   */
  autocomplete?: 'on' | 'off' = null;
  /**
   * `false`
   */
  firstVisual? = false;
  /**
   * `false`
   */
  onlyVisual? = false;
  /**
   * 
   */
  errors?: { [key: string]: string } = ERRORSDEFAULT;
  /**
   * 
   */
  ui?: SFUISchemaItem;
  /**
   *  `nzSize` 
   */
  size?: 'default' | 'large' | 'small';
  /**
   * 
   */
  button?: SFButton = {
    submit_type: 'primary',
    reset_type: 'default',
  };
  /**
   * date`type="string"`  `schema.format`  `ui.format` `YYYY-MM-DD HH:mm:ss`
   */
  uiDateStringFormat? = 'YYYY-MM-DD HH:mm:ss';
  /**
   * date`type="number"`  `schema.format`  `ui.format` `x` 13Unix Timestamp
   */
  uiDateNumberFormat? = 'x';
  /**
   * time`type="string"`  `schema.format`  `ui.format` `HH:mm:ss`
   */
  uiTimeStringFormat? = 'HH:mm:ss';
  /**
   * time`type="number"`  `schema.format`  `ui.format` `x` 13Unix Timestamp `1970-01-01`
   */
  uiTimeNumberFormat? = 'x';
}
