import { Observable } from 'rxjs';
import { FormProperty, PropertyGroup } from './model/form.property';

export const ERRORSDEFAULT = {
  'false schema':         ``,
  '$ref':                 `{ref}`,
  additionalItems:        `{ref}`,
  additionalProperties:   ``,
  anyOf:                  ` anyOf `,
  dependencies:           `{property}{deps}`,
  enum:                   ``,
  format:                 ``, // ` "{format}"`,
  type:                   ` {type}`,
  required:               ``,
  maxLength:              ` {limit} `,
  minLength:              ` {limit} `,
  minimum:                ` {comparison}{limit}`,
  formatMinimum:          ` {comparison}{limit}`,
  maximum:                ` {comparison}{limit}`,
  formatMaximum:          ` {comparison}{limit}`,
  maxItems:               ` {limit} `,
  minItems:               ` {limit} `,
  maxProperties:          ` {limit} `,
  minProperties:          ` {limit} `,
  multipleOf:             ` {multipleOf} `,
  not:                    ` "not" schema`,
  oneOf:                  ` "oneOf"  schema`,
  pattern:                ``,
  uniqueItems:            ` ( {j}  {i} )`,
  custom:                 ``,
  propertyNames:          ` "{propertyName}" `,
  patternRequired:        ` {missingPattern}`,
  switch:                 ` {caseIndex}  "switch" `,
  const:                  ``,
  contains:               ``,
  formatExclusiveMaximum: `formatExclusiveMaximum `,
  formatExclusiveMinimum: `formatExclusiveMinimum `,
  if:                     ` "{failingKeyword}"`,
};

export interface ErrorData {
  keyword: string;
  dataPath?: string;
  schemaPath?: string;
  params?: { [key: string]: any };
  message?: string;
  _custom?: boolean;
}

export interface ErrorSchema {
  /**
   * `true`
   * - `true` 
   * - `false` 
   */
  liveValidate?: boolean;
  /**
   *  `ErrorData.keyword` 
   */
  errors?: { [ key: string ]: string | ((obj: ErrorData) => string) };
  /**
   * `false`
   */
  firstVisual?: boolean;
  /**
   * `false`
   */
  onlyVisual?: boolean;
  /**
   *  `ERRORSDEFAULT`
   * -  `DelonSchemaFormConfig.ingoreKeywords`
   */
  ingoreKeywords?: string[];
  /**
   * 
   */
  validator?: (value: any, formProperty: FormProperty, form: PropertyGroup) => ErrorData[] | Observable<ErrorData[]>;
}
